import { UserResource } from "@clerk/types"
import { FileRowType, FilesCustomerIO } from "@rp/common/utils"

const mapFilesToCustomerIO = (
  data: FileRowType | undefined,
  user: UserResource | null | undefined,
): FilesCustomerIO => {
  if (!data) return { id: "", ownerIsUser: false, ownerId: "" }

  return {
    id: data.id,
    ownerIsUser: data.owner === user?.id,
    ownerId: data.owner,
  }
}

export { mapFilesToCustomerIO }
